// src/components/renderer/layouts/LayoutHero.tsx
import { SlideData, Language } from '@/core/types';
import { PhoneMockup } from '../PhoneMockup';

export const LayoutHero = ({ data, lang }: { data: SlideData; lang: Language }) => {
  const content = data.content[lang];

  return (
    <div className={`w-full h-full relative overflow-hidden flex flex-col items-center pt-20 bg-${data.themeColor}-50`}>
      {/* Arkaplan Deseni - Bu Layout'a özel */}
      <div className="absolute inset-0 opacity-10 bg-[radial-gradient(circle_at_center,_var(--tw-gradient-stops))] from-black to-transparent" />

      {/* Metin Alanı */}
      <div className="z-20 text-center space-y-4 max-w-2xl px-8">
        <span className="bg-white px-4 py-1 rounded-full text-sm font-bold shadow-sm uppercase tracking-widest">
            {content.subtitle}
        </span>
        <h1 className="text-7xl font-black text-slate-900 leading-tight">
          {content.title}
        </h1>
      </div>

      {/* Telefon (3D) */}
      <div className="absolute inset-0 flex items-center justify-center perspective-1000">
         {/* perspective-1000 sınıfı 3D efekti için şart */}
         <div className="relative w-full h-full flex items-center justify-center" style={{ perspective: '1000px' }}>
            <PhoneMockup phone={data.phone} image={data.image} />
         </div>
      </div>
    </div>
  );
};