// src/components/renderer/layouts/LayoutSplit.tsx
import { SlideData, Language } from '@/core/types';
import { PhoneMockup } from '../PhoneMockup';

export const LayoutSplit = ({ data, lang }: { data: SlideData; lang: Language }) => {
  const content = data.content[lang];

  return (
    <div className="w-full h-full relative overflow-hidden flex bg-slate-900 text-white">
        
      {/* Sol taraf: Metin */}
      <div className="w-1/2 h-full flex flex-col justify-center p-16 z-20">
        <h2 className="text-yellow-400 font-bold text-2xl mb-4">{content.subtitle}</h2>
        <h1 className="text-6xl font-black mb-8">{content.title}</h1>
        <div className="bg-white text-black px-8 py-4 rounded-xl text-xl font-bold w-max">
            {content.cta}
        </div>
      </div>

      {/* Sağ taraf: Telefon */}
      <div className="w-1/2 h-full relative" style={{ perspective: '1200px' }}>
         <div className="absolute top-1/2 left-0 -translate-y-1/2 w-full h-[800px]">
             <PhoneMockup phone={data.phone} image={data.image} />
         </div>
         {/* Bu tasarıma özel dekor */}
         <div className="absolute top-10 right-10 w-40 h-40 bg-yellow-400 rounded-full blur-[80px] opacity-50" />
      </div>
    </div>
  );
};