// src/app/page.tsx
"use client";

import { useEffect } from 'react';
import { useEditorStore } from '@/core/store';
import { SlideRenderer } from '@/components/renderer/SlideRenderer';
import { EditorPanel } from '@/components/editor/EditorPanel';
import { Plus } from 'lucide-react';

export default function PostFactoryPage() {
  const { slides, addSlide, activeSlideId, selectSlide, activeLanguage } = useEditorStore();

  // İlk açılışta boş bir slayt ekle
  useEffect(() => {
    if (slides.length === 0) {
      addSlide('hero-v1');
    }
  }, []);

  return (
    <div className="flex w-full h-screen bg-slate-50 overflow-hidden font-sans text-slate-900">
      
      {/* SOL: Slayt Listesi */}
      <div className="w-24 bg-white border-r border-slate-200 flex flex-col items-center py-4 gap-4 z-20">
        {slides.map((slide, index) => (
          <button
            key={slide.id}
            onClick={() => selectSlide(slide.id)}
            className={`w-16 h-16 rounded-xl border-2 transition-all overflow-hidden relative ${
              activeSlideId === slide.id ? 'border-blue-600 ring-2 ring-blue-100 scale-105' : 'border-slate-200 hover:border-slate-400'
            }`}
          >
            {/* Küçük Önizleme */}
            <div className="absolute inset-0 scale-[0.15] origin-top-left w-[1080px] h-[1350px] pointer-events-none">
                <SlideRenderer data={slide} lang={activeLanguage} />
            </div>
            <div className="absolute bottom-0 right-0 bg-black text-white text-[10px] px-1">{index + 1}</div>
          </button>
        ))}
        
        <button 
            onClick={() => addSlide('split-v1')} 
            className="w-12 h-12 rounded-full bg-slate-100 hover:bg-slate-200 flex items-center justify-center text-slate-600"
            title="Yeni Slayt Ekle"
        >
            <Plus size={24} />
        </button>
      </div>

      {/* ORTA: Canvas (Sahne) */}
      <div className="flex-1 bg-slate-100 flex items-center justify-center p-8 overflow-auto relative">
         {/* Arkaplan Grid */}
         <div className="absolute inset-0 opacity-[0.05]" style={{ backgroundImage: 'radial-gradient(#000 1px, transparent 1px)', backgroundSize: '20px 20px' }} />
         
         {activeSlideId && (
           <div className="transform scale-[0.65] shadow-[0_0_50px_rgba(0,0,0,0.1)] border border-slate-200">
              <SlideRenderer 
                data={slides.find(s => s.id === activeSlideId)!} 
                lang={activeLanguage}
              />
           </div>
         )}
      </div>

      {/* SAĞ: Editör Paneli */}
      <div className="z-20 shadow-xl">
        <EditorPanel />
      </div>

    </div>
  );
}